                        </el-form-item>
                    <el-form-item
                        :label="pushTypeDialog.title"
                        v-if="form.type && form.type==pushTypeDialog.type">
                        <div class="collection" @click="openPushTypeDialog">
                            <el-tag
                                class="u-course-tag"
                                v-for="item in pushTypeDialog.selectedData[this.pushTypeDialog.type]"
                                :key="item.id">
                                {{item.name}}
                            </el-tag>
                        </div>
                        </el-form-item>-->
                    <!-- <el-form-item label="">
                        <el-input style="width: auto;" v-model.number="form.score" type="number"  placeholder=""></el-input>
                        </el-form-item> -->
                    <el-form-item>
                        <el-button style="float: right" type="primary" @click="btnNextClick"></el-button>
                    </el-form-item>
                </el-form>
                
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <el-form label-width="120px" ref="form2" :model="form" :rules="rules2">
                   
                     <el-form-item prop="range" label="">
