                </el-form>
            </el-tab-pane>
        </el-tabs>

        <!--  -->
        <!-- <dialogSelectData ref="dialogSelect" v-model="dialogCourse.isShow" :getData="fetchCourse" title=""
                          :selectedList="form.course" @changeSelected="val=>form.course=val"  item-key="contentid">
            <div slot="search" class="course-search">
                <el-input @keyup.enter.native="$refs.dialogSelect.fetchCourse(true)" v-model="dialogCourse.course_name"
                          icon="search"
                          placeholder=""></el-input>
            </div>
        </dialogSelectData> -->

        <!-- ----------------------- -->
        <el-dialog  ref="dialogSelect" v-model="dialogTree.isShow" title=""
                           item-key="contentid">
            <!-- <div slot="search" class="course-search">
                <el-input @keyup.enter.native="$refs.dialogSelect.fetchCourse(true)" v-model="dialogCourse.course_name"
                          icon="search"
                          placeholder=""></el-input>
            </div> -->
            <!-- <CourseTree v-model="treeData" ref="courseCategory" :checkbox="true" ></CourseTree> -->
            <CourseTree v-model="treeData" :req="req" ref="courseCategory" :change="val=>form.range=val" :checkbox="true" :mark = this.mark ></CourseTree>
            <span slot="footer">
                <!-- <el-button type="warning" @click="resetChecked"></el-button> -->
                <el-button type="primary" @click="getCheckedNodes"></el-button>
            </span>
        </el-dialog >


        <!--  -->
        <el-dialog
            :title="pushTypeDialog.title"
            :visible.sync="pushTypeDialog.showDialog"
            v-if="pushTypeDialog.showDialog">
            <template v-if="pushTypeDialog.isSearch">
                <section class="search">
                    <section>
                        <i></i>
                        <DepSelect v-model="pushTypeDialog.fetchParam.gov_id" :change="getPushTypeData"></DepSelect>
                    </section>
                </section>
            </template>
            <Transfer placeholder=""
                      @searchFn="(val)=>{pushTypeDialog.page=1;pushTypeDialog.fetchParam.name=val;fetchPushTypeData();}"
                      @moreFn="()=>{pushTypeDialog.page++;fetchPushTypeData('no-clear');}"
                      :total="pushTypeDialog.total"
                      :data="pushTypeDialog.data"
                      :selectedValue='selectData'
                      v-model="pushTypeDialog.selectedData[pushTypeDialog.type]"></Transfer>
            <span slot="footer" class="dialog-footer">
                <el-button @click="pushTypeDialog.showDialog = false"> </el-button>
                <el-button type="primary" @click="transferConfirmFn"> </el-button>
            </span>
        </el-dialog>
