<script>
    import Transfer from '../../component/dialog/Transfer.vue'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInput.vue'
    import courseTaskService from '../../../services/gov/courseTaskService.js'
    import courseService from '../../../services/course/courseService.js'
    import commonService from '../../../services/commonService.js'
    import govService from '../../../services/gov/govService.js'
    import userService from '../../../services/gov/userService.js'
    import dialogSelectData from '../../component/dialog/SelectData4table.vue'
    import dialogTree from '../../component/dialog/dialogTree'
    import CourseTree from '../../component/tree/MenuTree.vue'
    import DateRange from '../../component/form/DateRangePicker.vue'
    import DepSelect from '../../component/select/Department.vue'


    export default{
        name: 'coursetask-template-add',
        computed: {
            id () {
                return this.$route.query.id
            },
        },
        data () {
            var validatePass = (rule, value, callback) => {
                if (value > this.form.single_score*this.form.single_num+this.form.multi_score*this.form.multi_num+this.form.judgment_score*this.form.judgment_num) {
                    callback(new Error(''))
                }
                callback()
            }
            return {
                activeTab: 'first',
                selectData:[],
                form: {                // 
                    title: void 0,          // 
                    category_id: void 0,       // 
                    image: void 0,        // 
                    description: void 0,  // 
                    // sort: void 0,         // 
                    // course_ids: [],     // 
                    // gov_ids: void 0,     // 
                    // user_ids: void 0,     // 
                    category_ids: [],     // 
                    // status: void 0,       // 
                    range: [],
                    // score: 0,     // 
                    // type:void 0,       //   ---
                    // stime:'',
                    // etime:'',
                    pass_score:void 0,
                    total_score:void 0,
                    total_subject:100,
                    total_score:100,
                    pass_score:void 0,
                    single_num:void 0,
                    single_score:void 0,
                    multi_num:void 0,
                    multi_score:void 0,
                    judgment_num:void 0,
                    judgment_score:void 0,
                },
                rules1: {
                    title:  [
                        {required: true,  message: '', trigger: 'blur'},
                        {
                            min: 1,
                            max: 40,
                            message: ' 40 '
                        },{
                            pattern:  /\S$/,
                            message: ''
                        }
                    ],
                    description: [{required: true, pattern:  /\S$/, min: 1,message: '', trigger: 'blur'}],
                    image: [{required: true, message: '', trigger: 'blur'}],
                    category_id: {type: 'number', required: true, message: '', trigger: 'change'},
                },
                  rules2: {
                    range: [{ required: true, message: '*'}],
                    pass_score: {required: true,validator: validatePass, trigger: 'blur'},
                    single_score: {type: 'number', required: true, message: '', trigger: 'change'},
                    multi_score	: {type: 'number', required: true, message: '', trigger: 'change'},
                    judgment_score: {type: 'number', required: true, message: '', trigger: 'change'},
                },
                dialogCourse: {
                    loading: false,
                    isShow: false,
                    course_name: void 0,
                },
                dialogTree:{
                    loading: false,
                    isShow: false,
                    course_name: void 0,
                },
                pushTypeDialog: { //
                    fetchParam: {
                        gov_id: '',
                        gov_ids: '',
                        name: ''
                    },
                    title: '',
                    isSearch: '',
                    type: '',
                    showDialog: false,
                    selectedData: {
                        2: [],
                        1: []
                    },
                    data: [],
                    page: 1,
                    pagesize: 15,
                    total: 0,
                },
                category_list:[],
                treeData: [],
                mark:{
                    type:'menu',
                    name:'name'
                },
                // limit:{
                //     s:void 0, //
                //     m:void 0, //
                //     j:void 0  //
                // },
                // limit:{
                    s:void 0, //
                    m:void 0, //
                    j:void 0, //
                // },
            }
        },
        watch:{
            'form.type'(){
                if(this.form.type==1){//
                    this.form.user_ids= ''
                }else{ //
                    this.form.gov_ids= ''
                }
            },
            'form.range'(){
                console.log(this.form.range) 
            },
            '$refs.courseCategory.$refs.tree'(){
                console.log(this.$refs.courseCategory.$refs.tree.getCheckedNodes());
            },

        },
        created () {
            xmview.setContentLoading(false)
            // console.log(this.$route.params.coursetaskInfo)
            if (this.$route.params.coursetaskInfo) {
                courseTaskService.getCourseTaskTemplateEditDetail(this.$route.query.id).then((ret) => {
                    this.form = Object.assign(this.form, ret.data)
                    // this.form.stime =  ret.data.start_date.split(' ')[0]
                    // this.form.etime =  ret.data.end_date.split(' ')[0]
                    // this.form.type = ret.data.type
                    // this.pushTypeDialog.type = ret.data.type
                    xmview.setContentTile(' ')
                    this.form.range = ret.data.category_ids.map(v=>{
                        v.contentid = v.category_ids
                        return v
                    }) 
                    console.log('selectedData : ', this.form.range)
                    this.$refs.dialogSelect.setSelected()
                    this.choosePushType()
                    if(ret.data.govs.length!==0){
                        this.pushTypeDialog.selectedData[this.pushTypeDialog.type] = this.generatorList(ret.data.govs || [])
                    }
                    else if(ret.data.users.length!==0){
                        this.pushTypeDialog.selectedData[this.pushTypeDialog.type] = this.generatorList(ret.data.users || [])
                    }
                   
                    xmview.setContentLoading(false)
                })
            }
            // this.getLimitNum()

            this.pushTypeDialog.selectedData[this.pushTypeDialog.type] = []
            this.getCategory()
        },
        methods: {
            getLimitNum(){
                this.s=this.form.single_num=40
                this.m=this.form.multi_num=30
                this.j=this.form.judgment_num=5
            },
             btnNextClick() {
                this.$refs.form1.validate((valid) => {
                    if (!valid) {
                        return false
                    }
                    if(this.activeTab=='first'){
                        this.activeTab = 'second' 
                        return
                    }
                 })
            },
            req(param){
                return courseService.getCategoryTree({ //
                    pid: -1,
                    level: -1,
                    pagesize:-1,
                })
            },
            // fetchCourseTree(params){
            //     return courseService.getPublicCourselist(Object.assign({}, this.dialogCourse, params))
            // },
            fetchCourseTree(){
                let param={
                        pid:-1, // 3- 
                        page:1,
                        level: -1,
                        pagesize: -1,
                    }
                return courseService.getCategoryTree(params).then((ret) => {
                        this.SecMenu=ret.data
                        // console.log('this.SecMenu+++++++',param,this.SecMenu)
                        xmview.setContentLoading(false)     
                    })
            },
            resetChecked(){
                console.log(this.$refs.courseCategory.$refs.tree);
                this.$refs.courseCategory.$refs.tree.setCheckedKeys([]);
            },
            getCheckedNodes(){
                this.dialogTree.isShow=false
                // console.log(this.$refs.courseCategory.$refs.tree.getCheckedNodes());
                let arr=this.$refs.courseCategory.$refs.tree.getCheckedNodes()
                console.log(11111111111,JSON.stringify(arr));
                
                    //ended==1
                    function func(arr, lable, box) {
                        arr.forEach(v => {
                            if (v.ended == 0) {
                                if(!v.children)xmview.showTip('error', "")
                                func(v.children, lable, box)
                            } else {
                                box.push(v)
                            }
                        })
                    }
                    let t = []
                    console.log(11111111111,arr);
                    console.log(11111111111,JSON.stringify(arr));
                    func(arr, 'children', t)
                    //
                    function format(t){
                        let obj = {};
                        let result = [];
                        t.forEach(v => {
                            obj[v.id] = v
                        });
                        for(let k in obj){
                            result.push(obj[k])
                        };
                        return result
                    }
                    console.log(11111111111,format(t))
                    this.form.range=format(t)
                    this.getCategoryCheck()
                    courseTaskService.getCategoryCheck({categorys:this.form.category_ids}).then((ret) => {
                        console.log('ret=====',ret);
                        xmview.showTip('success', '')
                        this.s=this.form.single_num=ret.single_num
                        this.m=this.form.multi_num=ret.multi_num
                        this.j=this.form.judgment_num=ret.trueorfalse_num

                    })

            },
            getCategoryCheck(){
                 let categorys=[] //
                    this.form.range.forEach((c) => {
                        categorys.push(c.contentid||c.id) //
                        // console.log(this.form.course_ids)
                    })
                    this.form.category_ids = categorys.join(',')
                    console.log(this.form)
            },
            getCategory(val){
                courseTaskService.getCategoryTree({pagesize:-1,type:2}).then((ret)=>{
                 this.category_list = ret.data;
                })
            },
            
            //
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                //  callback 
                cb(results);
            },
            transferConfirmFn () {
                this.pushTypeDialog.showDialog = false
            },
            //
            openPushTypeDialog () {
                this.pushTypeDialog.showDialog = true
                this.pushTypeDialog.page = 1
                this.pushTypeDialog.fetchParam.gov_id = ''
                this.pushTypeDialog.fetchParam.gov_ids = ''
                this.pushTypeDialog.fetchParam.name = ''
                this.fetchPushTypeData()
            },
            //
            choosePushType(){
                let map = {
                    1: {
                        type: '1',
                        label: '',
                        isSearch: false,
                    },
                    2: {
                        type: '2',
                        label: '',
                        isSearch: true,
                    }
                }
                let param = map[this.form.type]
                // console.log(param)
                if(param!=undefined){
                    this.pushTypeDialog.title = param.label
                    this.pushTypeDialog.isSearch = param.isSearch
                    this.pushTypeDialog.type = param.type
                    // console.log(this.pushTypeDialog.type)
                }

            },
            getPushTypeData () {
                this.pushTypeDialog.page = 1
                this.fetchPushTypeData()
            },
            fetchPushTypeData (type) {
                type !== 'no-clear' && (this.pushTypeDialog.data = [])
                let map = {
                    1: govService.getSelectList,  //
                    2: userService.fetchData,  //
                    // user_group: userService.userGroupSearch
                }
                let param = {
                    name: this.pushTypeDialog.fetchParam.name,
                    page: this.pushTypeDialog.page,
                    pagesize: this.pushTypeDialog.pagesize
                }

                if (this.pushTypeDialog.isSearch) {
                    param.gov_id = this.pushTypeDialog.fetchParam.gov_id
                    param.role_id = -1
                }
                console.log(param)
                map[this.pushTypeDialog.type](param).then(ret => {
                    // this.pushTypeDialog.total = ret._exts.total
                    this.pushTypeDialog.total = ret._exts.total
                    // if (this.pushTypeDialog.data.length > 0 && this.pushTypeDialog.data.length < ret._exts.total) {
                    if (this.pushTypeDialog.data.length > 0 ) {
                        this.pushTypeDialog.data.splice(-1, 1)
                    }
                    this.pushTypeDialog.data.push(...ret.data, {id: -1}) //id:-1 
                    // this.pushTypeDialog.data.push(...ret.data) //id:-1 
                    //  this.pushTypeDialog.data.forEach(item => { item.type = 'exam' })
                })
            },
            generatorList (arr) {
                return arr.map(item => {
                    return {
                        id: item['gov_id'] || item['user_id'] || item['group_id'],
                        name: item['gov_name'] || item['user_name'] || item['group_name']
                    }
                })
            },
            cropperFn(data, ext) {
                commonService.commonUploadImageBaseSection({
                    image: data,
                    alias: `${Date.now()}${ext}`
                }).then((ret) => {
                    xmview.showTip('success', '')
                    this.form.image = ret.url // 
                })
            },
            fetchCourse (params) {
                // { course_name = '', status, category_id , time_start, time_end, page, pagesize}
                return courseService.getPublicCourselist(Object.assign({}, this.dialogCourse, params))
            },
            
            submit(s) {
                 this.$refs.form1.validate((valid) => {
                    if (!valid) {
                        return false
                    }
                this.$refs.form2.validate((valid) => {
                    if (!valid) {
                        return false
                    }
                    // if()
                    // id
                    // this.form.course_ids = []
                    // this.form.course.forEach((c) => {
                    //     this.form.course_ids.push(c.id)
                    // })
                    console.log('this.form.range',this.form.range)  //
                    console.log('this.form.category_ids',this.form.category_ids)  //
                    // let categorys=[] //
                    // this.form.range.forEach((c) => {
                    //     categorys.push(c.contentid||c.id) //
                    //     // console.log(this.form.course_ids)
                    // })
                    // this.form.category_ids = categorys.join(',')
                    // console.log(this.form)

                    // if(this.form.type==1){
                    //     // govids
                    //     this.form.gov_ids = this.pushTypeDialog.type && this.pushTypeDialog.selectedData[this.pushTypeDialog.type].map(item => {
                    //     return item.id
                    //     }).join(',')
                    // }else{
                    //     // userids
                    //     this.form.user_ids = this.pushTypeDialog.type && this.pushTypeDialog.selectedData[this.pushTypeDialog.type].map(item => {
                    //     return item.id
                    //     }).join(',')
                    // }
                    // this.fetchParam.end_time = this.timeFormatter(this.fetchParam.end_time, true)

                    if (s > 0) { //
                        this.form.status = s
                    }
                    let reqFn = courseTaskService.addCourseTaskTemplate
                    if (this.form.id) {
                        reqFn = courseTaskService.updateCourseTaskTemplate
                    }
                    // console.log(this.form)
                    reqFn(this.form).then((ret) => {
                        xmview.showTip('success', '')
                        this.$router.push({name: 'gov-examtasktemplate'})
                    }).catch((ret) => {
                        xmview.showTip('error', ret.message)
                    })
                })
                })
            }
        },
        components: {DateRange,ImagEcropperInput, dialogSelectData, Transfer, DepSelect,CourseTree }
    }
</script>



